body {
  min-width: 400px;
  margin-top: 100px;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e8dccd;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Headers/General Stuff */

#innerHeader {
  text-align: center;
  font-family: Cambria, Georgia, serif; 
  color: #615047;
}

h1 {
  font-size:  40px;
  font-weight: bold;
}

h2 {
  font-size:  30px;
}

#innerBody {
  width: 400px;
  display: block;
  margin: 0 auto;
  background-color: transparent;
}

p {
  text-align: center;
  font-family: Cambria, Georgia, serif; 
  color: #50423a;
}

/* Inputs */

input {
  display: block;
  box-sizing: border-box;
  font-family: Cambria, Georgia, serif; 
  color: #50423a;
}

input:focus, select:focus{
  outline: none;
}

input[type="text"]{
  background-color : #fce3c3; 
  border-width: 0px;
  width: 300px;
  height: 60px;
  border-radius: 10px;
  margin: 0 auto;
  font-size: 28px;
  text-align: center;
  box-shadow: inset 0 0 0 3px #876e4e, inset 0 15px 0 15px #fce3c3;
}

input[type="submit"] {
  font-size: 24px;
  width: 150px;
  margin: 0 auto;
  box-shadow: inset 0 0 0 3px #876e4e, inset 0 25px 0 25px #e8dccd;
  border-radius: 10px;
  border-width: 0px;
  padding: 10px 0px;
}

select {
  display: block;
  width: 120px;
  margin: 0 auto;
  font-family: Cambria, Georgia, serif; 
  border: none;
  background-color: transparent;
  font-size:  26px;
  text-align: center;
  color: #50423a;
}

/* Image handling */

img {
  position:absolute;
}

.flipped {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.back {
  z-index: -1;
}

/* Arrow */

#arrow {
  height: 60px;
  top: 470px;
  left: 49%;
}

/* -> Animal 1 positioning */

#animal1{
  margin: 100px 0px;
  width: 400px;
  z-index: -2;
}

